import React, { useState } from 'react';
import { Button } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';

function App() {
    const [num, setNum] = useState(0);
    const increament = () => {
        setNum(num + 1);
    };
    const reset = () => {
        setNum(0);
    };
    const deacrement = () => {
        setNum(num - 1);
    };
    return (
        <div className='App'>
            <h1>{num}</h1>
            <div className='App__buttons'>
                <Button variant='success' onClick={increament}>
                    Increament
                </Button>
                <Button variant='warning' onClick={reset}>
                    Reset
                </Button>
                <Button variant='danger' onClick={deacrement}>
                    Decreament
                </Button>
            </div>
        </div>
    );
}

export default App;
